import React, {useState, useEffect} from 'react';
import * as s from './sidebar.styles';

const Sidebar = (props) => {

    const { sidebar_bg_img, sidebar_header, sidebar_menuitems } = props;

    const menuItems = sidebar_menuitems.map( (menuItem, index) => {
        return (
            <s.MenuItem key={index} >
                <s.MenuItemIMG src={`images/${menuItem.icon}`}/>
                <s.MenuItemTXT>{ menuItem.name }</s.MenuItemTXT>
                <s.SubMenu>{ menuItem.subMenuItems.map( (element, index) => {
                    return <s.SubMenuItem key={index}>{`${element.name} (${element.program})`}</s.SubMenuItem>;
                })}</s.SubMenu>
            </s.MenuItem>
        )
    });

    return <s.SidebarContainer sidebar_bg_img={sidebar_bg_img}>
        <s.SidebarHeader>{sidebar_header}</s.SidebarHeader>
        <s.SidebarContainerMenuitems>{menuItems}</s.SidebarContainerMenuitems>
    </s.SidebarContainer>
}

export default Sidebar;