import styled from '@emotion/styled';

export const SidebarContainer = styled.div`
        width: 280px;
        background-color: #9fa4c4;
        background-image: linear-gradient(315deg, rgba(252, 82, 150, 0.8) 0%, rgba(246, 112, 98, 0.8) 74%), url('images/${props => props.sidebar_bg_img}.svg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        box-shadow: -0.1px -0.1px 10px #d5d0d8; 
`;

export const SidebarHeader = styled.h3`
    padding: 20px 0;
    text-align: center;
    margin-bottom: 20px;
    font-size: 40px;
    font-family: 'Dancing Script', cursive;
    color: white;
    letter-spacing: 1px; 

`;

export const SidebarContainerMenuitems = styled.div`
   
    
`;

export const MenuItem = styled.div`
    padding: 6px 20px; 
    color: rgba(7, 29, 44, 0.9);


`;

export const MenuItemIMG = styled.img`
    position: relative;
    top: 2px;
    height: 18px;
    width: 18px;
    font-color: white;
    padding-right: 20px;
    

`;

export const MenuItemTXT = styled.p`
    font-size: 17px;
    display: inline;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;

    &:hover {
        color: rgba(255, 255, 255);
        transition: 0.1s ease-in all;
    };
`;

export const SubMenu = styled.div`
    padding: 10px 0;
`;

export const SubMenuItem = styled.div`
&:hover {
    color: rgba(255, 255, 255);
    transition: 0.1s ease-in all;
};
`;

